@import url("https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400;700&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Ubuntu", sans-serif;
}

h3 {
  color: #3f6c29;
}

:root {
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --greyText: hsl(0, 0%, 75%);
  --inputColor: hsl(330, 12%, 97%);
  /* Font ang Typography */
  --biggestFontSize: 2.5rem;
  --h1FontSize: 1.5rem;
  --h2FontSize: 1.25rem;
  --h3FontSize: 1rem;
  --normalFontSize: 0.938rem;
  --smallFontSize: 0.813rem;
  --smallestFontSize: 0.75rem;
  --borderRadius: 5px;
}

body {
  background: var(--bgColor);
}

.flex {
  display: flex;
  align-items: center;
}

.grid {
  display: grid;
  align-items: center;
  gap: 1.5rem;
}

html {
  font-size: 90%;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

.icon {
  font-size: var(--h2FontSize);
  cursor: pointer;
}

img,
video {
  height: auto;
  width: 100%;
}

.message {
  display: none;
}

.showMessage {
  display: block;
  color: var(--whiteColor);
  padding: 10px;
  background: rgb(255, 0, 0);
  border-radius: 3px;
  text-align: center;
}

.btn {
  border: none;
  outline: none;
  cursor: pointer;
  border-radius: var(--borderRadius);
  padding: 0.8rem 1rem;
  color: var(--whiteColor);
  background: #3f6c29;
}
.btn:hover {
  transition: 0.3s ease;
}

.secTitle {
  display: flex;
  text-align: center;
  padding: 1rem 0;
  font-size: 25px;
  font-weight: 700;
  color: #3f6c29;
  flex-direction: column;
  gap: 1rem;
}
.secTitle p {
  color: var(--blackColor);
  font-size: var(--normalFontSize);
  font-weight: 400;
}

.btn .icon {
  margin-left: 0.5rem;
}

.btn:hover {
  color: #3f6c29;
  background: #A2CA71;
}

.section {
  padding: 4rem 0 2rem;
}

.container {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

@media screen and (min-width: 860px) {
  :root {
    --biggestFontSize: 3.5rem;
    --h1FontSize: 2.5rem;
    --h2FontSize: 2.25rem;
    --h3FontSize: 2.5rem;
    --normalFontSize: 2.5rem;
    --smallFontSize: 1.5rem;
    --borderRadius: 5px;
  }
  .secTitle {
    padding: 1rem 0 3rem;
    font-size: var(--biggestFontSize);
    font-weight: 700;
    color: #3f6c29;
    flex-direction: column;
    gap: 1rem;
  }
  .secTitle p {
    color: var(--blackColor);
    font-size: var(--h2FontSize);
    font-weight: 400;
  }
}
@media screen and (min-width: 1024px) {
  .section {
    padding: 4rem 0 2rem;
  }
  .container {
    margin: auto;
    width: 75%;
  }
}