:root {

  --bgColor: #f5f5f5;
  --primaryColor: #3f6c29 ;
  --primaryColorLight:#A2CA71;
  --formColor: #354952;
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --greyText: hsl(0, 0%, 75%);
  --inputColor: hsl(330, 12%, 97%);

     --biggestFontSize: 2.5rem;
     --h1FontSize: 1.5rem;
     --h2FontSize: 1.25rem;
     --h3FontSize: 1rem;
     --normalFontSize: 0.938rem;
     --smallFontSize: 0.813rem;
     --smallestFontSize: 0.75rem;
     --borderRadius: 5px;
   }
   
   .discount {
     width: 100%;
   }
   .discount .secContainer {
     height: 400px;
     width: 100%;
     position: relative;
     display: flex;
     align-items: center;
     justify-content: center;
   }
   .discount .secContainer video {
     height: 100%;
     width: 100%;
     -o-object-fit: cover;
        object-fit: cover;
   }
   .discount .secContainer .textDiv {
     position: absolute;
     display: grid;
     padding: 2rem;
     color: white;
     text-align: center;
     gap: 1rem;
   }
   .discount .secContainer .textDiv .title {
     font-size: 18px;
     font-weight: 700;
   }
   .discount .secContainer .textDiv p {
     opacity: 1;
   }
   .discount .secContainer .textDiv .input_btn {
     flex-direction: column;
     gap: 1rem;
   }
   .discount .secContainer .textDiv .input_btn input {
     background: transparent;
     padding: 0.8rem;
     border: 2px solid var(--whiteColor);
     outline: none;
     color: white;
     font-size: 13px;
     width: 95%;
     border-radius: 5px;
   }
   .discount .secContainer .textDiv .input_btn input::-moz-placeholder {
     color: white;
     font-size: 12px;
   }
   .discount .secContainer .textDiv .input_btn input::placeholder {
     color: white;
     font-size: 12px;
   }
   .discount .secContainer .textDiv .input_btn .btn {
     background: rgba(236, 218, 209, 0.4980392157);
     border: none;
     font-size: 15px;
     padding: 1rem 2rem;
     width: 95%;
     color: white;
     transition: 0.3s ease;
   }
   .discount .secContainer .textDiv .input_btn .btn:hover {
     background: #3f6c29;
     color: white;
   }
   
   @media screen and (min-width: 520px) {
     .discount .secContainer .textDiv {
       gap: 2rem;
     }
     .discount .secContainer .textDiv .title {
       font-size: 25px;
     }
     .discount .secContainer .textDiv p {
       font-size: 18px;
       max-width: 500px;
       padding: 0 3rem;
     }
     .discount .secContainer .textDiv .input_btn .btn {
       font-size: 16px;
     }
   }
   @media screen and (min-width: 1024px) {
     .discount .secContainer .textDiv {
       gap: 2rem;
     }
     .discount .secContainer .textDiv .title {
       font-size: 30px;
     }
   }