:root {

  --bgColor: #f5f5f5;
  --primaryColor: #3f6c29 ;
  --primaryColorLight:#A2CA71;
  --formColor: #354952;
  --whiteColor: hsl(0, 0%, 100%);
  --blackColor: hsl(0, 0%, 18%);
  --greyText: hsl(0, 0%, 75%);
  --inputColor: hsl(330, 12%, 97%);

    /* Font ang Typography */
    --biggestFontSize: 2.5rem;
    --h1FontSize: 1.5rem;
    --h2FontSize: 1.25rem;
    --h3FontSize: 1rem;
    --normalFontSize: 0.938rem;
    --smallFontSize: 0.813rem;
    --smallestFontSize: 0.75rem;
    --borderRadius: 5px;
  }
  
  .home {
    height: 90vh;
    width: 100%;
    padding: 5rem 1.5rem;
    background: linear-gradient(rgba(0, 0, 1, 0.42), rgba(0, 0, 1, 0.549)), url("../../../assets/banner.jpg");
    background-position: center;
    -o-object-fit: cover;
       object-fit: cover;
    background-size: cover;
    background-repeat: no-repeat;
    display: flex;
    align-items: center;
    row-gap: 3rem;
  }
  .home .spanText {
    color: white;
    font-weight: 500;
  }
  .home .homeTitle {
    color: white;
    padding: 2rem 0;
    font-size: var(--biggestFontSize);
  }
  .home .homeTitle strong {
    font-weight: 800;
  }
  .home .btn {
    background: transparent;
    border: 2px solid var(--whiteColor);
    color: var(--whiteColor);
    font-size: var(--h3FontSize);
    transition: 0.3s;
    width: -moz-max-content;
    width: max-content;
  }
  .home .btn:hover {
    background: #CCD3CA;
    color: #3f6c29;
    border: 2px solid var(--whiteColor);
  }
  
  @media screen and (min-width: 690px) {
    .home .homeText {
      transform: translateX(5rem);
    }
    .home .spanText {
      font-weight: 600;
      font-size: 20px;
    }
    .home .homeTitle {
      color: white;
      padding: 2rem 0;
      font-size: 45px;
    }
    .home .btn {
      border: 2px solid white;
      color: white;
      font-size: 15px;
      transition: 0.3s;
    }
  }
  @media screen and (min-width: 840px) {
    .home .homeText {
      transform: translateX(10rem);
    }
  }
  @media screen and (min-width: 1240px) {
    .home .homeText {
      transform: translateX(20rem);
    }
    .home .homeText .spanText {
      font-size: 25px;
    }
    .home .homeText .homeTitle {
      font-size: 70px;
    }
    .home .homeText .btn {
      font-size: 22px;
    }
  }